        if (firstCodeNum === '4' || firstCodeNum === '5') {
            // do something
            // console.log('is 4xx or 5xx')

            return Promise.reject('nooooo')
        }

        //  request.stop()
        if (JSON.stringify(res) === '{"errMsg":"request:fail abort"}') {
            // do something
            // return Promise.reject('xxxxxxxxx');
            return false;
        }

